import React, { Component } from 'react'

export default class Todoing extends Component {
    constructor(props) {    
        super(props)
          
    } 

    delD=(e)=>{
        this.props.del(e.target.value);
    }

    cheD=(e)=>{
        this.props.cek(e.target.value)
    }

   
    
    render() {
        var {list}=this.props;
        console.log(list);
        return (
            <div>
                <h2>正在进行</h2>        
                <ul>
                {            
                    list.map((value, key) => {
                        console.log(value.checked);              
                        if (!value.checked) {                
                            return (                  
                                <li key={key}>                    
                                    <input type='checkbox' checked={value.checked} onChange={this.cheD} />
                                    {value.title}-----------
                                    <button onClick={this.delD}>删除</button>
                                </li>                
                            )              
                        }            
                    })          
                }        
                </ul>        
                <h2>已经完成</h2>        
                <ul>         
                {            
                    list.map((value, key) => {              
                        if (value.checked) {                
                            return (                  
                            <li key={key}>                    
                                <input type='checkbox' checked={value.checked} onChange={this.cheD} />
                                {value.title}-----------
                                <button onClick={this.delD}>删除</button>
                            </li>                
                            )              
                        }            
                    })          
                }        
                </ul>   
            </div>
        )
    }
}
